document.getElementById('csvFiles').addEventListener('change', function csvInput() { //Wenn File eingefgt luft das hier
  document.getElementById('title').innerHTML = "Ldt";
  oldData = true; //oldData bool fr stop button
  completeFile = [];  
  let reader = new FileReader(); //FileReader von JS
  reader.readAsText(document.getElementById('csvFiles').files[0]);
  reader.onload = function (event) { //wird gelesen und wenn fertig, csvVerarbeitung aufgerufen mit gelesener Datei
    csvVerarbeitung(event.target.result);
  };
});
var wertVorher = 0;
var wertNachher = 0;
var savedCom = [];
var laenge = 0;
function csvVerarbeitung(inputFile) { //input noch als String wird aufgeteilt in Blcke getrennt durch MS: Zeilen
  let csvAlsArray = inputFile.slice(inputFile.indexOf("MS:")).split("MS:");
  //console.log(csvAlsArray);
  var tempArray = [];
  var tempZwei = [];
  var rn = false;
  
  if(csvAlsArray[1].indexOf("\r\n") != -1) {  //Manche CSV Files haben am Zeilenende \r\n und manche nur \n       ?
    rn = true;
  }
  else {      //damit falls rn dann split ber rn und sonst split ber n
    rn = false;
  }


  for(var i=0; i<csvAlsArray.length; i++) { //Fr jeden Eintrag 1 Mal:
    if (rn) {

      tempArray = csvAlsArray[i].split("\r\n");  //MS Block in neues Arrays getrennt durch Zeilenumbrche
    }
    else {

      tempArray = csvAlsArray[i].split("\n");
    }

    if (tempArray.length == 20) { //Wenn genau 20 Zeilen -> Kein Fehler:
      tempArray.pop();  //letzter Eintrag wird entfernt (ist Counter vor nchstem "MS:")
      tempZwei = [];
      tempArray[0] = tempArray[0].slice(0,tempArray[0].indexOf("M:")-1); //erster Eintrag im Array wird gekrzt von MS: XXXXX M: XXXXX H: XXXXX,,,, auf nur XXXXX Zahl von MS
      for (var j=0; j<tempArray.length; j++) {  //Fr jeden Eintrag (Zeile):
        tempZwei = tempZwei.concat(tempArray[j].split(","));  //Werte in neues Array getrennt durch Kommas
      }
      if (tempZwei.length == 109) { //Falls 109 Eintrge (1 MS Wert & 108 DruckWerte):
        //tempZwei[0] = tempZwei[0].slice(0,tempZwei[0].indexOf("M:")-1);
        wertVorher = wertNachher;
        wertNachher = 0;
        for (var w=1; w<109; w++) {
          wertNachher += Number(tempZwei[w]);
        }
        if (wertNachher < wertVorher * 1.5) {
          completeFile = completeFile.concat(tempZwei); //completeFile wird zu completeFile + Fehlerloser Datensatz
        }
        
      }                                              //"Fehlerlos" Hinsichtlich Eintrgen, Fehlerhafte Werte sind noch mglich
    }
    //console.log(i + "/" + csvAlsArray.length);

  }
  //console.log("DONE");
  //console.log(completeFile);
  if (/*document.getElementById("graphi").checked*/graphIsOn) { //Wenn Graph Box hkchen
    //graphIsOn = true;
    document.getElementById("chartOn").style.display = "inline";  //Graphen visible
    graphIt(completeFile);                                        //Berechnung fr Graphen durchfhren
  }
  else {
    document.getElementById("chartOn").style.display = "none";  //sonst Graphen nicht anzeigen
  }
  document.getElementById('title').innerHTML = "Schuhsohle";    //Fertig geladen
  savedCom = Array.from(completeFile);                        //Backup des Array fr die Rcksprnge
  laenge = savedCom.length;                                 //Lnge des Arrays
  progressbar.style.display = "inline";   //Anzeigen der Progressbar
  displayIt();  //Alles verarbeitet und in 1 riesen Array, jetzt Anzeigen lassen
  
}

